'use client';
import { useState } from 'react';
import { format, isPast } from 'date-fns';
import { Card, CardContent, CardHeader, CardTitle, CardDescription, CardFooter } from '@/components/ui/card';
import { Badge } from '@/components/ui/badge';
import { Button } from '@/components/ui/button';
import { Check, X, Loader2, Calendar, Clock, User } from 'lucide-react';
import { useToast } from '@/hooks/use-toast';
import type { Event, Role, RoleStatus } from '@/types';
import { cn } from '@/lib/utils';
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from '@/components/ui/tooltip';

interface ScheduleItemCardProps {
  event: Event;
  role: Role;
  onUpdateStatus: (eventId: string, roleId: string, status: RoleStatus) => Promise<void>;
}

export default function ScheduleItemCard({ event, role, onUpdateStatus }: ScheduleItemCardProps) {
  const { toast } = useToast();
  const [isUpdating, setIsUpdating] = useState(false);
  const eventIsInPast = isPast(event.eventDate);

  const handleUpdate = async (status: 'Confirmed' | 'Declined') => {
    setIsUpdating(true);
    try {
      await onUpdateStatus(event.id, role.id, status);
      toast({
        title: `Response Sent`,
        description: `You have ${status.toLowerCase()} the role of ${role.roleName}.`,
      });
    } catch (error) {
      toast({
        variant: 'destructive',
        title: 'Error',
        description: 'Failed to update status. Please try again.',
      });
    } finally {
      setIsUpdating(false);
    }
  };

  return (
    <Card className={cn("transition-all", eventIsInPast && "opacity-60 bg-muted/50")}>
      <CardHeader>
        <div className="flex justify-between items-start">
            <div>
                <CardTitle className="font-headline text-xl">{event.eventName}</CardTitle>
                <CardDescription className="flex flex-wrap items-center gap-x-4 gap-y-1 text-sm mt-1">
                    <span className="flex items-center gap-1.5"><Calendar className="h-4 w-4" /> {format(event.eventDate, 'EEEE, MMM d')}</span>
                    <span className="flex items-center gap-1.5"><Clock className="h-4 w-4" /> {format(event.eventDate, 'p')}</span>
                </CardDescription>
            </div>
            <TooltipProvider>
                <Tooltip>
                    <TooltipTrigger asChild>
                      <Badge className={cn('text-xs font-semibold capitalize', 
                          role.status === 'Confirmed' && 'bg-primary text-primary-foreground border-primary',
                          role.status === 'Pending' && 'bg-accent text-accent-foreground border-accent',
                          role.status === 'Declined' && 'bg-destructive text-destructive-foreground border-destructive'
                      )}>
                          {role.status}
                      </Badge>
                    </TooltipTrigger>
                    <TooltipContent>
                        {eventIsInPast ? 'This event is in the past.' : `Your response is ${role.status}.`}
                    </TooltipContent>
                </Tooltip>
            </TooltipProvider>
        </div>
      </CardHeader>
      <CardContent>
        <div className="flex items-center gap-2 bg-background p-3 rounded-md border">
            <User className="h-5 w-5 text-primary" />
            <div>
                <p className="text-sm text-muted-foreground">Your Role</p>
                <p className="font-semibold">{role.roleName}</p>
            </div>
        </div>
      </CardContent>
      {role.status === 'Pending' && !eventIsInPast && (
        <CardFooter className="flex justify-end gap-2">
          <Button 
            variant="destructive"
            onClick={() => handleUpdate('Declined')}
            disabled={isUpdating}
          >
            {isUpdating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <X className="mr-2 h-4 w-4" />}
            Decline
          </Button>
          <Button 
            onClick={() => handleUpdate('Confirmed')}
            disabled={isUpdating}
            className="bg-accent text-accent-foreground hover:bg-accent/90"
          >
            {isUpdating ? <Loader2 className="mr-2 h-4 w-4 animate-spin" /> : <Check className="mr-2 h-4 w-4" />}
            Accept
          </Button>
        </CardFooter>
      )}
    </Card>
  );
}
