
'use client';

import Link from 'next/link';
import { usePathname, useRouter } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Church, Users, Calendar, LogOut, ShieldCheck, BookCopy, CalendarCheck } from 'lucide-react';
import { cn } from '@/lib/utils';
import {
  DropdownMenu,
  DropdownMenuContent,
  DropdownMenuItem,
  DropdownMenuLabel,
  DropdownMenuSeparator,
  DropdownMenuTrigger,
} from '@/components/ui/dropdown-menu';
import { Avatar, AvatarFallback } from '@/components/ui/avatar';
import { useAuth } from '@/hooks/use-auth';
import { Switch } from '@/components/ui/switch';
import { Label } from '@/components/ui/label';

export function Header() {
  const pathname = usePathname();
  const router = useRouter();
  const { isAdmin, toggleAdmin, logout } = useAuth();

  const handleLogout = () => {
    logout();
    router.push('/');
  };

  const navLinks = isAdmin
    ? [
        { href: '/admin', label: 'Admin Console', icon: ShieldCheck },
        { href: '/admin/volunteers', label: 'Volunteers', icon: Users },
        { href: '/admin/templates', label: 'Templates', icon: BookCopy },
      ]
    : [
        { href: '/my-schedule', label: 'My Schedule', icon: Calendar },
        { href: '/my-availability', label: 'My Availability', icon: CalendarCheck },
      ];

  const userInitial = isAdmin ? 'A' : 'V';
  const userName = isAdmin ? 'Admin User' : 'Volunteer User';

  return (
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-14 items-center">
        <Link href={isAdmin ? "/admin" : "/my-schedule"} className="mr-6 flex items-center space-x-2">
          <Church className="h-6 w-6 text-primary" />
          <span className="font-bold font-headline sm:inline-block">Weaver's Loom</span>
        </Link>
        <nav className="flex items-center space-x-2">
          {navLinks.map(({ href, label, icon: Icon }) => (
            <Button
              key={href}
              variant={pathname.startsWith(href) ? 'secondary' : 'ghost'}
              className={cn(
                'justify-start',
                pathname.startsWith(href) && 'font-bold'
              )}
              asChild
            >
              <Link href={href}>
                <Icon className="mr-2 h-4 w-4" />
                <span className="hidden sm:inline">{label}</span>
              </Link>
            </Button>
          ))}
        </nav>
        <div className="ml-auto">
          <DropdownMenu>
            <DropdownMenuTrigger asChild>
               <Button variant="ghost" className="relative h-8 w-8 rounded-full">
                <Avatar className="h-8 w-8">
                  <AvatarFallback>{userInitial}</AvatarFallback>
                </Avatar>
              </Button>
            </DropdownMenuTrigger>
            <DropdownMenuContent className="w-56" align="end" forceMount>
               <DropdownMenuLabel className="font-normal">
                <div className="flex flex-col space-y-1">
                  <p className="text-sm font-medium leading-none">{userName}</p>
                  <p className="text-xs leading-none text-muted-foreground">
                    {isAdmin ? 'admin@example.com' : 'volunteer@example.com'}
                  </p>
                </div>
              </DropdownMenuLabel>
              <DropdownMenuSeparator />
               <div className="px-2 py-1.5 text-sm">
                <div className="flex items-center justify-between">
                  <Label htmlFor="admin-mode" className="flex items-center gap-2 cursor-pointer">
                    <ShieldCheck className="h-4 w-4" /> Admin Access
                  </Label>
                  <Switch
                    id="admin-mode"
                    checked={isAdmin}
                    onCheckedChange={toggleAdmin}
                  />
                </div>
              </div>
              <DropdownMenuSeparator />
              <DropdownMenuItem onClick={handleLogout}>
                <LogOut className="mr-2 h-4 w-4" />
                <span>Log out</span>
              </DropdownMenuItem>
            </DropdownMenuContent>
          </DropdownMenu>
        </div>
      </div>
    </header>
  );
}
