// Using a custom hook to manage state and simulate backend
export function useMockDatabase() {
  const [events, setEvents] = useState<Event[]>(initialEvents);
  const [volunteers] = useState<Volunteer[]>(initialVolunteers);
  const [universalRoles, setUniversalRoles] = useState<UniversalRole[]>(initialUniversalRoles);
  const [serviceTemplates, setServiceTemplates] = useState<ServiceTemplate[]>(initialServiceTemplates);
  const [availability, setAvailability] = useState<VolunteerAvailability[]>(initialAvailability);


  const getEvents = useCallback(async (churchId: string): Promise<Event[]> => {
    return events.filter(event => event.churchId === churchId);
  }, [events]);

  const createEvent = useCallback(async (newEventData: Omit<Event, 'id' | 'roles' | 'churchId'>, templateId?: string): Promise<Event> => {
    let newRoles: Role[] = [];
    if (templateId) {
        const template = serviceTemplates.find(t => t.id === templateId);
        if (template) {
            newRoles = template.roles.map(templateRole => ({
                id: `role_${Date.now()}_${templateRole.instanceId}`,
                roleName: templateRole.name,
                assignedVolunteerId: null,
                assignedVolunteerName: null,
                status: 'Pending',
            }));
        }
    }

    const newEvent: Event = {
      ...newEventData,
      id: `event_${Date.now()}`,
      churchId: MOCK_CHURCH_ID,
      roles: newRoles,
    };
    setEvents(prev => [...prev, newEvent]);
    return newEvent;
  }, [serviceTemplates]);

  const updateEvent = useCallback(async (eventId: string, updatedData: Partial<Event>): Promise<void> => {
    setEvents(prev => prev.map(event => event.id === eventId ? { ...event, ...updatedData } : event));
  }, []);

  const addRoleToEvent = useCallback(async (eventId: string, roleData: Omit<Role, 'id'>): Promise<Role> => {
    const newRole: Role = {
      ...roleData,
      id: `role_${Date.now()}`,
    };
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return { ...event, roles: [...event.roles, newRole] };
      }
      return event;
    }));
    return newRole;
  }, []);

  const updateRoleInEvent = useCallback(async (eventId: string, roleId: string, updatedRoleData: Partial<Role>): Promise<void> => {
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return {
          ...event,
          roles: event.roles.map(role => role.id === roleId ? { ...role, ...updatedRoleData } : role),
        };
      }
      return event;
    }));
  }, []);

  const deleteRoleFromEvent = useCallback(async (eventId: string, roleId: string): Promise<void> => {
    setEvents(prev => prev.map(event => {
      if (event.id === eventId) {
        return { ...event, roles: event.roles.filter(role => role.id !== roleId) };
      }
      return event;
    }));
  }, []);
  
  const getMyAssignments = useCallback(async (volunteerId: string): Promise<Event[]> => {
    return events
      .map(event => ({
        ...event,
        roles: event.roles.filter(role => role.assignedVolunteerId === volunteerId),
      }))
      .filter(event => event.roles.length > 0);
  }, [events]);

  const updateAssignmentStatus = useCallback(async (eventId: string, roleId: string, status: RoleStatus): Promise<void> => {
    await updateRoleInEvent(eventId, roleId, { status });
  }, [updateRoleInEvent]);
  
  const getVolunteers = useCallback(async (): Promise<Volunteer[]> => {
    return volunteers;
  }, [volunteers]);

  const getVolunteersSync = useCallback((): Volunteer[] => {
    return volunteers;
  }, [volunteers]);

  // Universal Roles CRUD
  const getUniversalRoles = useCallback(async (): Promise<UniversalRole[]> => {
    return universalRoles;
  }, [universalRoles]);
  
  const addUniversalRole = useCallback(async (name: string): Promise<UniversalRole> => {
      const newRole: UniversalRole = { id: `urole_${Date.now()}`, name };
      setUniversalRoles(prev => [...prev, newRole]);
      return newRole;
  }, []);

  const deleteUniversalRole = useCallback(async (id: string): Promise<void> => {
      setUniversalRoles(prev => prev.filter(r => r.id !== id));
      // Also remove from any templates
      setServiceTemplates(prev => prev.map(t => ({...t, roles: t.roles.filter(role => role.roleId !== id) })));
  }, []);

  // Service Templates CRUD
  const getServiceTemplates = useCallback(async (): Promise<ServiceTemplate[]> => {
    return serviceTemplates;
  }, [serviceTemplates]);

  const createServiceTemplate = useCallback(async (name: string, roles: TemplateRole[]): Promise<ServiceTemplate> => {
      const newTemplate: ServiceTemplate = { id: `template_${Date.now()}`, name, roles };
      setServiceTemplates(prev => [...prev, newTemplate]);
      return newTemplate;
  }, []);

  const updateServiceTemplate = useCallback(async (id: string, updates: Partial<Omit<ServiceTemplate, 'id'>>): Promise<void> => {
      setServiceTemplates(prev => prev.map(t => t.id === id ? {...t, ...updates} : t));
  }, []);
  
  const deleteServiceTemplate = useCallback(async (id: string): Promise<void> => {
      setServiceTemplates(prev => prev.filter(t => t.id !== id));
  }, []);

  // Volunteer Availability
  const getVolunteerAvailability = useCallback(async (volunteerId: string): Promise<Date[]> => {
      const record = availability.find(a => a.volunteerId === volunteerId);
      return record ? record.unavailableDates : [];
  }, [availability]);

  const toggleVolunteerAvailability = useCallback(async (volunteerId: string, date: Date): Promise<void> => {
      setAvailability(prev => {
          const availabilityRecord = prev.find(a => a.volunteerId === volunteerId);
          if (availabilityRecord) {
              const dateExists = availabilityRecord.unavailableDates.some(d => isSameDay(d, date));
              const updatedDates = dateExists 
                  ? availabilityRecord.unavailableDates.filter(d => !isSameDay(d, date))
                  : [...availabilityRecord.unavailableDates, date];
              
              return prev.map(a => a.volunteerId === volunteerId ? { ...a, unavailableDates: updatedDates } : a);
          } else {
              // Create a new record if it doesn't exist
              const newRecord: VolunteerAvailability = { volunteerId, unavailableDates: [date] };
              return [...prev, newRecord];
          }
      });
  }, []);


  return { 
    events, 
    getEvents,
    createEvent,
    updateEvent,
    addRoleToEvent,
    updateRoleInEvent,
    deleteRoleFromEvent,
    getMyAssignments, 
    updateAssignmentStatus,
    getVolunteers,
    getVolunteersSync,
    universalRoles,
    getUniversalRoles,
    addUniversalRole,
    deleteUniversalRole,
    serviceTemplates,
    getServiceTemplates,
    createServiceTemplate,
    updateServiceTemplate,
    deleteServiceTemplate,
    getVolunteerAvailability,
    toggleVolunteerAvailability,
    MOCK_CHURCH_ID,
    MOCK_VOLUNTEER_ID,
  };
}
