'use client';
import { useMockDatabase } from '@/lib/data';
import { Header } from '@/components/layout/Header';
import MyScheduleList from '@/components/volunteer/MyScheduleList';
import { useAuth } from '@/hooks/use-auth';
import { useRouter } from 'next/navigation';
import { useEffect } from 'react';

export default function MySchedulePage() {
  const db = useMockDatabase();
  const { isLoggedIn, isAdmin } = useAuth();
  const router = useRouter();

   useEffect(() => {
    if (!isLoggedIn) {
      router.push('/');
    } else if (isAdmin) {
      router.push('/admin');
    }
  }, [isLoggedIn, isAdmin, router]);

  const myAssignments = db.events
    .map(event => ({
      ...event,
      roles: event.roles.filter(role => role.assignedVolunteerId === db.MOCK_VOLUNTEER_ID),
    }))
    .filter(event => event.roles.length > 0)
    .sort((a, b) => a.eventDate.getTime() - b.eventDate.getTime());
    
  if (!isLoggedIn || isAdmin) {
    return null; // Or a loading/redirecting component
  }

  return (
    <div className="flex min-h-screen w-full flex-col bg-muted/40">
      <Header />
      <main className="flex-1 p-4 md:p-8">
        <div className="max-w-4xl mx-auto">
          <h1 className="text-3xl font-bold font-headline tracking-tight mb-6">
            My Schedule
          </h1>
          <MyScheduleList 
            assignments={myAssignments} 
            onUpdateStatus={db.updateAssignmentStatus} 
          />
        </div>
      </main>
    </div>
  );
}
